<template>
  <div class="signup">
    <v-form
      @submit.prevent="signUp"
      ref="signUpForm"
      class="my-2"
    >
      <v-row>
        <v-col cols="3" class="mx-auto">
          <v-text-field
            label="Enter username"
            v-model="signUpForm.username"
          />
          <v-text-field
            label="Enter your fio"
            v-model="signUpForm.fio"
          />
          <v-text-field
            label="Enter stajh raboty"
            v-model="signUpForm.stajh_raboty"
          />
          <v-text-field
            label="Enter your email"
            v-model="signUpForm.email"
          />
          <v-text-field
            label="Enter password"
            v-model="signUpForm.password"
            type="password"
          />
          <v-btn type="submit" color="brown" dark>SignUp</v-btn>
        </v-col>
      </v-row>
    </v-form>
  </div>
</template>

<script>
export default {
  name: 'SignUp',
  data: () => ({
    signUpForm: {
      username: '',
      stajh_raboty: '',
      password: '',
      fio: '',
      email: ''
    }
  }),
  methods: {
    signUp () {
      try {
        this.axios.post('http://127.0.0.1:8000/auth/users/', this.signUpForm)
          .then(response => {
            if (response.status !== 201) {
              throw new Error(response.status)
            }
            this.$refs.signUpForm.reset()
            this.$router.push('/login')
          })
        this.$refs.signUpForm.reset()
      } catch (e) {
        console.error('AN API ERROR', e)
      }
    }
  }
}
</script>

